}

.toggle__fill {
  width: var(--width);
  height: var(--height);
  border-radius: var(--border-radius);
  background: #ddd;
  transition: background .2s;
  position: relative;
}

.toggle__fill:after {
  content: "";
  height: var(--height);
  width: var(--height);
  border-radius: var(--border-radius);
  background: #fff;
  transition: transform .2s;
  position: absolute;
  top: 0;
  left: 0;
  box-shadow: 0 0 10px rgba(0, 0, 0, .25);
}

.toggle__input:checked ~ .toggle__fill {
  background: #009578;
}

